import React, { useEffect, useState } from 'react'
import api from '../../../../../../../../../../Utils/api';
import ExaminerNav from '../../../../../../NavExaminerGlobal/AddStudentNav/ExaminerNav'
import ExamStudentList from './ExamStudentList/ExamStudentList'
import { useLocation } from 'react-router-dom';

const ExamStudents = () => {
  
    const location = useLocation();

    const id = location.state.courseID;

    const [ examStudents, setExamStudents ] = useState([]);

    useEffect(()=>{
    api
        .get(`examiner/students/${location.state.courseID}`,{headers : { Authorization : `${localStorage.getItem('accessToken')}`}})
        .then((result)=>{
              setExamStudents(result.data.data.students)
        })
        .catch((err)=>console.log(err,'examStudents'))
      },[])
        console.log(examStudents,'examStudents')
  return (
    <div>
        <ExaminerNav />
        <ExamStudentList id = {id} studentsList={examStudents} />
    </div>
  )
}

export default ExamStudents