import React from 'react'
import './Que.css'

import { useLocation, useNavigate } from 'react-router-dom'


import DeleteIcon from '@mui/icons-material/Delete';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';

const Que = () => {
    const location = useLocation();
    const ques = location.state.data;

    const navigate = useNavigate();

  return (
    <div className='que'>
        <div className='que-heading'>
            <div className='que-back'>
                <ArrowBackIcon sx={{mt : 2}} /><p onClick={()=>navigate('/examiner/subjectlist/createexam',{state : {courseID : location.state.courseID}})}>Go Back</p>
            </div>
            <h2>Question Paper</h2>
        </div>
        {ques.map((x,ind)=>
            <div className='que-data' key={x.id}>
                <div className='que-data-detail'>
                    <div className='que-data-Question'>
                        <p>{ind + 1}.{x.question}</p>
                    </div>
                    <div className='que-data-option'>
                            <p><span>(a)</span> {x.options[0]}</p>
                            <p><span>(b)</span> {x.options[1]}</p>
                            <p><span>(c)</span> {x.options[2]}</p>
                            <p><span>(d)</span> {x.options[3]}</p>
                    </div>
                    <div className='que-data-correct'>
                            <p><span>Correct Ans : </span> {x.correctOption}</p> 
                            <p><span>Que Marks : </span> {x.marks}</p>    

                    </div>
                </div>
                <div className='que-dta-remove'>
                        <DeleteIcon sx={{color: '#4f3f39'}} />
                </div>
                
            </div>
        )}

    </div>
  )
}

export default Que