import React, { useState } from 'react'
import './ExamStudentList.css'
import studentImg from '../../../../../../../../../../../Assets/Global/laptop2.jpg'
import CloseIcon from '@mui/icons-material/Close';
import DoneIcon from '@mui/icons-material/Done';

import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import { useNavigate } from 'react-router-dom';
import api from '../../../../../../../../../../../Utils/api';
import { toast } from 'react-toastify';
import { Button } from '@mui/material';

const ExamStudentList = (props) => {
    
console.log(props.studentsList, "STUD");

    const navigate = useNavigate();

    const [ check, setCheck ]  = useState(false);

    const [ students, setStudents]=  useState([])
 
    const checkHandler = (x) =>{
        setCheck(!check);
       
        if(!check){
            console.log('true')
        }
        else{
            console.log('check is false')
        }
    }

    const DoneHandler = () =>{
        api
                .post('/examiner/result',students,{ headers : { Authorization : `${localStorage.getItem('accessToken')}`}})
                .then((result)=>{
                    toast(result.data.message)
                })
                .catch((err)=>console.log(err,'ExamStuentList'))
    }
    


  return (
    <div className='exam-add-student'>
        <div className='exam-add-student-heading'>
            <div className='exam-add-back'>
                <ArrowBackIcon sx={{mt : 2}} />
                <p onClick={()=>navigate('/examiner/subjectlist/createexam',{state : { courseID : props.id }})}>Go Back</p>
            </div>
            <h2>Student List</h2>
            
        </div>
        { props.studentsList.map((x)=>
        <div className='exam-add-student-data' key={x.id}>
          <div className='exam-add-student-data-detail'>
              <div className='exam-add-student-data-img'>
                  <img src={studentImg} alt='studentimg' />
              </div>
              <div className='exam-add-student-data-info'>
                <h5>{x.firstName} {x.lastName}</h5>
                <p>{x.email}</p>
              </div>
          </div>
          <div className='exam-add-student-data-course'>
              <h4>{x.course}</h4>
          </div>
          <div className='exam-add-student-data-button'>
            <>
                {
                !check ?
                <CloseIcon  onClick={()=>checkHandler(x._id)} />
                :
                <DoneIcon onClick={()=>checkHandler(x._id)} />
                }
            </>
          </div>
        </div>
            )
        }
        
        <div className='exam-student-btn'>
            <Button variant='contained' sx={{mt:5}} onClick={DoneHandler} >Done</Button>
        </div>
    </div>
  )
}

export default ExamStudentList