import { Button, TextField } from '@mui/material'
import React, { useState } from 'react'
import './ExamQue.css'

import { useLocation, useNavigate } from 'react-router-dom'
import api from '../../../../../../../../Utils/api'
import { toast } from 'react-toastify'




const ExamQue = (props) => {
    const[date,setDate] = useState();
    const[start,setStart] = useState();
    const[end,setEnd] = useState();

    const location = useLocation();

    const navigate = useNavigate();
    const [ queData, setQueData ] = useState({
        question : '',
        optionOne : '',
        optionTwo : '',
        optionThree : '',
        optionFour : '',
        marks : '',
        correctAns : '',
    });
    
    const changeHandler = (e) => {
        setQueData(oldData => {
            return {...oldData, [e.target.name]: e.target.value}
        });
    }

    const questions = [];

    const QueSubmitHandler = (e) =>{
        const {optionOne,optionTwo,optionThree,optionFour} = queData;
        const questionData = { question : queData.question,options : [optionOne,optionTwo,optionThree,optionFour],correctOption : queData.correctAns }
       
        questions.push(questionData);
        e.preventDefault();
        console.log(questions)
    }

    const SubmitExamHandler = () =>{
        const body = {
            subjectID: props.ids.subjectID,
            examDate : date,
            startTime: start,
            endTime :end,
            questions : questions,
            
        }
        api
            .post('/examiner/exam',{ headers : {Authorization : `${localStorage.getItem('accessToken')}`}})
            .then((result)=>{

                toast(result.data.message)
            })
    }


  return (
    <div className='create-exam'>
        <div className='create-exam-heading'>
            <h2>Create Exam</h2>
        </div>
        <form className='create-exam-data' onSubmit={QueSubmitHandler}>
            <div className='create-exam-que'>
                <TextField variant='outlined' label='question' name='question' onChange={changeHandler} sx={{width : 800}} />
            </div>
            <div className='create-exam-option'>
                <TextField variant='outlined' label='option-one' sx={{ mr:4, mt:2, mb:2 }} name="optionOne"  onChange={changeHandler} />
                <TextField variant='outlined' label='option-two' sx={{ mr:4 , mt:2,mb:2 }} name="optionTwo" onChange={changeHandler}/>
                <TextField variant='outlined' label='option-three' sx={{ mr:4, mt:2,mb:2  }} name="optionThree" onChange={changeHandler} />
                <TextField variant='outlined' label='option-four' sx={{ mr:4, mt:2,mb:2  }} name="optionFour" onChange={changeHandler}/>
            </div>
            <div className='create-exam-correct'>
                <TextField variant='outlined' label='que-marks' name='marks' sx={{ mb : 2}} onChange={changeHandler} />
                <TextField variant='outlined' label='correct-ans' name='correctAns' sx={{ mb : 2}} onChange={changeHandler} />
            </div>
            <div className='create-exam-button'>
                <Button type='submit' variant='contained' sx={{mt:2,mb:2}}>Add Que</Button>
            </div>
        </form>
      
        <div className='create-exam-buttons'>
            <Button type='submit' variant='contained' sx={{mr:2}} onClick={()=>navigate('/examiner/subjectlist/createexam/questions',{state : {data : questions,courseID : props.ids.courseID || location.state.courseID}})}>show questions</Button>
            <Button type='submit' variant='contained' sx={{mr:2}} onClick={()=>navigate('/examiner/subjectlist/createexam/addstudents',{state : {courseID : props.ids.courseID || location.state.courseID}})}>Add Students</Button>
                
        </div>
        <div className='create-exam-duration'>
            <div className='create-exam-duration-dt'>
                <p>Exam Date : </p> <input type='date' onChange={(e)=>setDate(e.target.value)}/>
                <p>Start Time :</p> <input type='time' onChange={(e)=>setStart(e.target.value)}  />
                <p>End Time</p><input type='time' onChange={(e)=>setEnd(e.target.value)} />   
            </div>
            
           <div className='create-exam-duration-submit'>
                <Button variant='contained' onClick={SubmitExamHandler}>Submit Exam</Button>
           </div>


        </div>
    </div>
  )
}

export default ExamQue